$breakpoints: (
	"xs": 0,
	"sm": 480px,
	"md": 700px,
	"lg": 960px,
	"xl": 1200px,
);
@mixin xs {
	@media (min-width: map-get($breakpoints, "xs")) {
		@content;
	}
}
@mixin sm {
	@media (min-width: map-get($breakpoints, "xs")) {
		@content;
	}
}
@mixin md {
	@media (max-width: map-get($breakpoints, "md")) {
		@content;
	}
}
@mixin lg {
	@media (min-width: map-get($breakpoints, "lg")) {
		@content;
	}
}
@mixin xl {
	@media (min-width: map-get($breakpoints, "xl")) {
		@content;
	}
}
